@import url(./style.css);

header {
    position: fixed;
    right: 0;
    left: 0;
    top: 0;
    transition: background-color 0.3s ease;
    z-index: 1;

    .head1 {
        border-bottom: 1px solid var(--fourth-color);
        background-color: rgba(20, 20, 20, 0.5);

        .row {
            width: 1240px;
            margin: auto;
            padding: 5px 20px;
            border: 0;

            .column {
                flex: 1;
            }

            i {
                color: var(--fourth-color);
            }

            .social {
                justify-content: right;
                text-align: right;

                i {
                    background-color: var(--fourth-color);
                    color: var(--primary-color);
                    border-radius: 5px;
                    font-size: 17px !important;
                    padding: 5px;
                }
            }
        }
    }

    .head {
        width: 1240px;
        margin: auto;
        padding: 15px 20px;
        border: 0;

        .column {
            margin: auto 0;
        }

        img {
            width: 200px;
        }
    }

    #off,
    #on {
        display: none;
    }

    ul {
        list-style-type: none;
        display: flex;
        gap: 10px;
        justify-content: right;
    }

    ul li {
        padding: 2px 10px;
        color: var(--fourth-color);
        cursor: pointer;
    }
    
    a {
        font-size: 18px;
        color: var(--fourth-color);
    }

    ul li:hover,
    ul li.active {
        text-decoration: underline;
        color: var(--fourth-color);
    }
}

header.scrolled {
    background-color: rgba(20, 20, 20, 0.8);
}

.bg1 {
    background-image: linear-gradient(to right, rgba(20, 20, 20, 0.3), rgba(20, 20, 20, 0.3)), url(../asset/hero1.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    border-radius: 0 0 50px 50px;

    h1 {
        color: var(--fourth-color);
    }

    .row {
        padding: 200px 20px 0 20px;
    }

    .column {
        flex: 1;
    }
}

.booking {
    #show {
        display: block !important;
    }

    .tabs {
        width: fit-content;
        background-color: var(--secondary-color);
        padding: 10px;
        border-radius: 10px 10px 0 0;
        margin-top: 20px;

        ul {
            list-style-type: none;
            display: flex;
            gap: 10px;

            li {
                background-color: transparent;
                padding: 0 15px;
                border-radius: 5px;
                color: var(--fourth-color);
                cursor: pointer;
            }

            li:hover,
            .active {
                background-color: var(--primary-color);
            }
        }
    }

    h2 {
        margin: 0;
        padding: 0;

        i {
            color: var(--primary-color);
            font-size: 22px;
        }
    }

    .form {
        display: none;
        background-color: rgba(255, 255, 255, 0.5);
        padding: 25px;
        border-radius: 0 10px 10px 10px;
        box-shadow: 0 2px 2px rgba(20, 20, 20, 0.3);
        margin-bottom: -55px;

        form {
            margin-top: 15px;
        }

        button {
            margin: 5px 0;
        }
    }
}

form {
    ::placeholder {
        font-size: 17px;
        color: #4b4b4b;
        padding-left: 10px;
    }

    input,
    select,
    textarea,
    .passenger-info {
        font-family: var(--secondary-font);
        width: 100%;
        padding: 15px 10px;
        margin: 5px 0;
        border: none;
        border-radius: 5px;
        font-size: 16px;
        background-color: var(--fourth-color);
    }

    label {
        flex: 1;
        font-family: var(--secondary-font);
        font-size: 16px;
        text-align: left;
    }

    .passenger {
        width: 100%;
        height: auto;
    }

    button {
        width: 100%;
    }

    div {
        gap: 5px;
    }

    .passenger-selection {
        padding: 20px;
        position: absolute;
        box-shadow: 0px 0 2px 2px rgba(20, 20, 20, 0.2);
        background-color: var(--fourth-color);
        border-radius: 10px;
        display: none;
    }

    .passenger-counter {
        display: flex;
        align-items: center;
    }

    .passenger-counter label {
        margin-right: 10px;
    }

    .passenger-counter button {
        border: 1px solid #ccc;
        background-color: var(--secondary-color);
        cursor: pointer;
    }
}

.intro {
    p {
        font-size: 18px;
        text-align: center;
        width: 60%;
        margin: 0 auto;
    }
}

.uspsection {
    background-color: rgba(20, 20, 20, 0.1);
    border-radius: 50px;

    .row {
        gap: 20px;
    }

    .left {
        background-image: url(../asset/media1.jpg);
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        border-radius: 10px;
        width: 48%;
        padding: 20px;

        div {
            border: 2px dashed var(--primary-color);
            height: 100%;
            border-radius: 10px;
            padding: 125px 20px;
        }
    }

    .data {
        display: grid;
        grid-template-columns: 1fr 1fr;
        text-align: center;
        gap: 20px;

        div {
            flex: 1;
        }

        .uspcol {
            background-color: rgba(205, 44, 34, 0.2);
            padding: 20px;
            border-radius: 15px;
            transition: transform 0.3s ease, box-shadow 0.3s ease;
            cursor: pointer;

            img {
                width: 75px;
                height: 75px;
            }
        }

        .uspcol:hover {
            background-color: rgba(139, 62, 234, 0.2);
            transform: scale(1.05);
            box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
        }
    }
}

.offer {
    .column {
        background-color: rgba(139, 62, 234, 0.2);
        border-radius: 15px;
    }

    .right {
        flex: 1;
        background-color: var(--primary-color);
        border-radius: 0 15px 15px 0;
        padding: 40px;

        h2,
        p {
            padding: 0;
            color: var(--fourth-color);
        }
    }

    form button {
        width: 30%;
        background-color: var(--secondary-color);
    }

    form button:hover {
        background-color: var(--fourth-color);
        color: var(--secondary-color);
    }

    .left {
        flex: 1;
        padding: 55px 40px;
        text-align: center;

        img {
            width: 250px;
        }

        h3 {
            color: var(--primary-color);
            margin: 15px 0;
        }
    }
}

.services {
    background-image: linear-gradient(to right, rgba(20, 20, 20, 0.5), rgba(20, 20, 20, 0.5)), url(../asset/service-BG.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    clip-path: polygon(0 5%, 100% 0, 100% 95%, 0 100%);
    text-align: center;
    background-attachment: fixed;

    h2 {
        color: var(--fourth-color);
    }

    .img {
        margin-top: 20px;
        background-color: rgba(255, 255, 255, 0.7);
        border-radius: 10px;

        img {
            width: 100%;
            border-radius: 10px 10px 0 0;
        }

        .details {
            padding: 20px;
            border-radius: 10px;
        }
    }
}

.mainslider {

    .slider {
        position: relative;
        overflow: hidden;
    }

    .slider-wrapper {
        display: flex;
        transition: transform 0.5s ease-in-out;
        gap: 10px;
    }

    .slide {
        border-radius: 10px;
        background-color: rgba(139, 62, 234, 0.2);

        img {
            width: 375px;
            border-radius: 10px 10px 0 0;
            height: auto;
        }

        div {
            padding: 20px;
            text-align: center;
        }

        p {
            font-size: 15px;
        }
    }

    .nav-button {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        background-color: transparent;
        border: none;
        cursor: pointer;
        color: #fff;
        font-size: 24px;
        background-color: var(--primary-color);
        padding: 5px 13px;
        border-radius: 50px;
    }

    .prev {
        left: 10px;
    }

    .next {
        right: 10px;
    }
}

.deals {
    .row {
        background-image: linear-gradient(to right, rgba(20, 20, 20, 0.2), rgba(20, 20, 20, 0.2)), url(../asset/deals-bg.webp);
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        padding: 100px 40px;
        border-radius: 20px;

        h2 {
            color: var(--fourth-color);
        }
    }

    .slide1 {
        border-radius: 10px;
        background-color: rgba(255, 255, 255, 0.85);
        padding: 20px;
        min-width: 350px;

        p {
            font-size: 15px;
        }
        

        div {
            padding: 0;
            text-align: left;
        }

        .img {
            border-radius: 10px 10px 0 0;
            height: auto;
            p {
                text-align: left;
                width: fit-content;
                padding: 0 10px;
                margin-top: 10px;
                border-radius: 5px;
                color: var(--fourth-color);
                background-color: var(--primary-color);
            }

            h4 {
                text-align: left;
                font-size: 18px;
                font-weight: 700;
            }

            div {
                margin: auto;
            }

            img {
                border-radius: 100%;
                width: 100px;
                height: 100px;
                border: 2px solid var(--secondary-color);
                padding: 2px;
            }
        }

        .main {
            margin-top: 20px;
        }

        .icons {

            /* text-align: left !important; */
            /* justify-content: right !important; */
            i {
                color: var(--secondary-color);
                border-radius: 50px;
            }
        }

        .price {
            text-align: center;

            h3 {
                color: var(--primary-color);
            }
        }

        .note {
            background-color: var(--primary-color);
            border-radius: 5px;
            color: var(--fourth-color);
            text-align: center;
            margin-top: 15px;
            padding: 5px;
        }
    }
}

footer {
    text-align: center;

    img {
        width: 225px;
        background-image: linear-gradient(135deg, var(--primary-color), var(--secondary-color));
        padding: 15px;
        border-radius: 10px;
    }

    .column {
        flex: 1;
    }

    .social {
        padding-top: 30px;

        i {
            background-color: var(--primary-color);
            color: var(--fourth-color);
            padding: 10px;
            border-radius: 50px;
        }

        i:hover {
            background-color: var(--secondary-color);
            color: var(--fourth-color);
        }
    }

    a {
        color: var(--primary-color);
    }

    a:hover {
        color: var(--secondary-color);
        text-decoration: underline;
    }

    .foot {
        padding-top: 20px;
        margin-top: 50px;
        border-top: 1px solid #000;

    }
}

.cta {
    background-image: linear-gradient(to right, rgba(20, 20, 20, 0.8), rgba(20, 20, 20, 0.8)), url(../asset/CTA-bg.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    border-radius: 50px 50px 0 0;
    text-align: center;

    h2,
    h3,
    p {
        color: var(--fourth-color);
    }

    i {
        font-size: 35px;
        background-color: var(--fourth-color);
        color: var(--primary-color);
        padding: 15px;
        border-radius: 50%;
    }

    .column {
        margin: 0 auto;
        width: 70%;
    }
}

.blog {
    text-align: center;

    .blogs {
        flex: 1 !important;
        box-shadow: 0 0 3px 3px rgba(20, 20, 20, 0.3);
        border-radius: 10px;
        margin-top: 20px;
        padding: 20px;

        img {
            border-radius: 10px;
            width: 100%;
            height: auto;
        }

        div {
            padding: 20px;
            text-align: center;
        }
    }
}

.mobilecta {
    position: fixed;
    bottom: 0;
    right: 0;
    left: 0;
    margin: 10px !important;
    padding: 15px 10px;
    border-radius: 10px;
    display: none;
    background-color: var(--secondary-color);

    .column {
        gap: 15px;
    }

    div {
        margin: auto 0;
    }

    p, h3 {
        color: var(--fourth-color);
        line-height: 1.5;
        font-size: 15px;
    }

    .img {
        border: 2px solid red;
        border-radius: 100%;
        padding: 2px;
    }

    img {
        width: 60px;
        background-color: #fff;
        border-radius: 50%;
    }

    i {
        color: var(--fourth-color);
        background-color: var(--primary-color);
        padding: 12px;
        border-radius: 50px;
        font-size: 25px;
    }
}

.about {
    background-color: rgba(139, 62, 234, 0.1);
    border-radius: 50px;

    .column {
        flex: 1;
        margin: auto 0;
    }

    img {
        width: 100%;
    }
}